import {
	setUserInfo,
	getUserInfo
} from './auth'
import {
	getOpenid
} from '@/api/user';
export function wxLogin() {
	let _this = this
	return new Promise(async (resolve) => {
		//
		let userInfo = getUserInfo()
		//
		if (userInfo && userInfo.openId) {
			resolve('ok')
		} else {
			// code
			uni.getUserProfile({
				desc: '',
				success: (loginRes) => {
					// uni.login({
					// 	provider: 'weixin',
					// 	success: function(loginRes) {
					console.log('loginRes', loginRes)
					let userInfo = loginRes.userInfo
					// 
					uni.getSetting({
						async success(res) {
							console.log("", res);
							if (!res.authSetting['scope.userInfo']) {
								//
								resolve('authSetting')
							} else {
								//
								uni.login({
									provider: 'weixin',
									success: async (res) => {
										console.log("");
										//openId 
										let result = await getOpenid({
											js_code: res.code
										})
										Object.assign(userInfo,{
											openId:result.openid
										})
										setUserInfo(userInfo);
										resolve('ok')
									},
									fail: e => {
										_this.title = e
									}
								})

							}
						},
						fail: function(res) {
							console.log(res)
							resolve('error')
						}
					})
				}
			});
		}
	})
}
